/*      */
.split-screen-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

/*       */
.split-container {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: flex;
}

/*      */
.split-horizontal {
  flex-direction: column;
}

/*      */
.split-vertical {
  flex-direction: row;
}

/*       */
.split-pane {
  flex: 1;
  position: relative;
  min-height: 100px;
  min-width: 100px;
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

/*      */
.split-content {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: auto;
  box-sizing: border-box;
}

/*      */
.split-pane .split-screen-container {
  width: 100%;
  height: 100%;
  border: none;
}

/*      */
.resizer {
  background-color: #ebedf4;
  border: 1px solid #d1d3e2;
  position: absolute;
  z-index: 10;
  transition: background-color 0.2s ease;
}

/*   */
.resizer.horizontal {
  height: 8px;
  width: 100%;
  cursor: row-resize;
  left: 0;
  z-index: 10;
  top: calc(50% - 4px);
}

/*   */
.resizer.vertical {
  width: 8px;
  height: 100%;
  cursor: col-resize;
  top: 0;
  z-index: 10;
  left: calc(50% - 4px);
}

/*     */
.resizer:hover {
  background-color: #4e73df;
  border-color: #3a5ccc;
}

/*     */
.resizer.active {
  background-color: #4e73df;
  border-color: #3a5ccc;
}

/*    ,     */
.split-pane .resizer {
  background: #d0d0d0;
  border-color: #c0c0c0;
}

.split-pane .split-pane .resizer {
  background: #c0c0c0;
  border-color: #b0b0b0;
}

.split-pane .split-pane .split-pane .resizer {
  background: #b0b0b0;
  border-color: #a0a0a0;
}

/*       */
.split-screen-controls {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  gap: 5px;
  z-index: 100;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 4px;
  padding: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  opacity: 0;
  transition: opacity 0.2s ease;
}

/*       */
.split-screen-container:hover > .split-screen-controls {
  opacity: 1;
}

/*      */
.split-control-button {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 3px;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 12px;
  padding: 0;
  color: #666;
}

.split-control-button:hover {
  background: #f5f5f5;
  color: #333;
}

/*      */
.split-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  text-align: center;
  color: #999;
  font-style: italic;
  font-size: 14px;
}

/*      */
.split-controls {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 100;
  display: flex;
  gap: 5px;
  opacity: 0;
  transition: opacity 0.3s;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 4px;
  padding: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

/*        */
.split-screen-container:hover .split-controls {
  opacity: 1;
}

/*      */
.split-control-btn {
  width: 24px;
  height: 24px;
  border: none;
  border-radius: 4px;
  background: #fff;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
  transition: all 0.2s;
}

.split-control-btn:hover {
  background: #f0f0f0;
}

/*      */
.split-pane .chart-container {
  width: 100% !important;
  height: 100% !important;
}

/*  canvas  ,    */
.split-pane canvas {
  width: 100% !important;
  height: 100% !important;
}

/*       */
.split-pane .chart-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

/*      */
.split-pane .chart {
  flex: 1;
  width: 100%;
  position: relative;
}

/*      */
.split-pane .chart-title,
.split-pane .chart-controls {
  margin: 5px;
}

/*      */
.quad-split {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  width: 100%;
  height: 100%;
}

.quad-split .split-pane {
  border: 1px solid #ccc;
}

/* -    */
@media (max-width: 768px) {
  .split-screen-controls {
    top: 5px;
    right: 5px;
  }
  
  .split-control-btn {
    width: 20px;
    height: 20px;
  }
}

/*  ,    */
.split-screen-container[data-active="true"],
.split-screen-container.active-container {
  outline-offset: -2px;
  background-color: rgba(75, 115, 223, 0.05);
  z-index: 5; /*  z-index    */
}

/*      */
.active-container {
  box-shadow: inset 0 0 8px rgba(75, 115, 223, 0.3);
}

/*    */
.split-screen-container:hover {
  outline: 2px dashed rgba(75, 115, 223, 0.3);
  outline-offset: -2px;
  background-color: rgba(75, 115, 223, 0.02);
}

/*      */
/*         */
.split-container {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: flex;
}

/*   -     */
.split-container.split-horizontal {
  flex-direction: column;
}

/*   -     */
.split-container.split-vertical {
  flex-direction: row;
}

/*    */
/*       */
.split-container.split-horizontal .resizer.horizontal {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 8px;
  margin-top: -4px;
  cursor: row-resize;
  z-index: 100;
}

/*       */
.split-container.split-vertical .resizer.vertical {
  position: absolute;
  left: 50%;
  top: 0;
  width: 8px;
  height: 100%;
  margin-left: -4px;
  cursor: col-resize;
  z-index: 100;
}

/*     */
.split-container.split-horizontal > .split-pane:first-child {
  height: calc(50% - 4px);
  margin-bottom: 8px;
}

.split-container.split-horizontal > .split-pane:last-child {
  height: calc(50% - 4px);
  margin-top: 0;
}

/*     */
.split-container.split-vertical > .split-pane:first-child {
  width: calc(50% - 4px);
  margin-right: 8px;
}

.split-container.split-vertical > .split-pane:last-child {
  width: calc(50% - 4px);
  margin-left: 0;
} 